// src/app/page.js
'use client';
import React, { useState } from 'react';
import {
  MenuFoldOutlined,
  MenuUnfoldOutlined,
  UploadOutlined,
  UserOutlined,
  VideoCameraOutlined,
} from '@ant-design/icons';
import { Button, Layout, Menu, theme } from 'antd';
import { useRouter } from 'next/router';
import './page.css';

const { Header, Sider, Content } = Layout;

const App = () => {
  const [collapsed, setCollapsed] = useState(false);
  const router = useRouter();
  const {
    token: { colorBgContainer, borderRadiusLG },
  } = theme.useToken();

  const handleMenuClick = (e) => {
    router.push(`/${e.key}`);
  };

  return (
    <Layout style={{ height: '100vh' }}>
      <Sider trigger={null} collapsible collapsed={collapsed}>
        <div className="logo">Web3Hub</div>
        <Menu
          theme="dark"
          mode="inline"
          defaultSelectedKeys={['1']}
          onClick={handleMenuClick}
          items={[
            {
              key: 'index',
              icon: <UserOutlined />,
              label: '首页',
            },
            {
              key: 'market-analysis',
              icon: <VideoCameraOutlined />,
              label: '市场分析',
            },
            {
              key: 'learning-center',
              icon: <UploadOutlined />,
              label: '学习中心',
            },
            {
              key: 'user-center',
              icon: <UploadOutlined />,
              label: '用户中心',
            },
          ]}
        />
      </Sider>
      <Layout>
        <Header
          style={{
            padding: 0,
            background: colorBgContainer,
          }}
        >
          <Button
            type="text"
            icon={collapsed ? <MenuUnfoldOutlined /> : <MenuFoldOutlined />}
            onClick={() => setCollapsed(!collapsed)}
            style={{
              fontSize: '16px',
              width: 64,
              height: 64,
            }}
          />
        </Header>
        <Content
          style={{
            margin: '24px 16px',
            padding: 24,
            minHeight: 280,
            background: colorBgContainer,
            borderRadius: borderRadiusLG,
          }}
        >
          {/* 动态加载内容 */}
          {router.pathname === '/' && <div>首页内容</div>}
          {router.pathname === '/market' && <div>市场分析内容</div>}
          {router.pathname === '/learning' && <div>学习中心内容</div>}
          {router.pathname === '/user' && <div>用户中心内容</div>}
        </Content>
      </Layout>
    </Layout>
  );
};

export default App;